<template>
	<view class="hbox">
		<h1 class="title">历史记录</h1>
		<block v-for="(item, index) in 3" :key="index">
			<tn-list-cell @click="handClick(item.vId)" padding="0rpx 0rpx">
				<view class="vBox">
					<div class="movie-poster">
						<img :src=item alt="Movie Poster">
					</div>
					<div class="movie-info">
						<h5 class="movie-title">{{item}}</h5>
						<h4 class="movie-note">{{item}}</h4>
						<p class="movie-actors">演员：{{item}}</p>
						<p class="movie-release">上映时间：{{item}}</p>
						<tn-line-progress :height=6 :round=false :percent="50"
							activeColor="#01BEFF"></tn-line-progress>
					</div>
				</view>
			</tn-list-cell>
		</block>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				hlist:[]
			}
		},
		methods: {
			querryAllData() {
			    let selectSql = "select * from history";
			    // 注意这里使用的是 selectSql，不是 executeSql
			    plus.sqlite.selectSql({
			        name: "vhistory",
			        sql: selectSql,
			        success: (res) => {
			            this.hlist=res;
			        },
			        fail: (e) => {
			            console.log(e);
			            this.log = '查询数据失败';
			        }
			    });
			},
		},
		onLoad() {
			this.querryAllData();
		},
	}
</script>

<style scoped>
	.hbox {
		height: 100vh;
		background-color: #3e3e3e;
	}

	.title {
		padding: 18rpx;
		margin: auto 20rpx;
		color: white;
		font-size: 36rpx;
		border-bottom: 0.5rpx double orangered;
	}

	.vBox {
		display: flex;
		background-color: #3e3e3e;
		width: 100%;
	}

	.movie-poster {
		flex: 30%;
		padding: 10px;
	}

	.movie-poster img {
		border: 1px solid #C1C1C1;
		box-shadow: 2px 2px 2px #6F6F6F;
		border-radius: 10%;
		width: 100%;
		height: auto;
	}

	.movie-info {
		overflow: hidden;
		/* 超出部分隐藏 */
		flex: 70%;
		padding: 10px;
		color: white;
		border-bottom: 1px solid orangered;
	}

	.movie-title {
		font-size: 18px;
		font-weight: bold;

	}

	.movie-actors,
	.movie-release,
	{
	white-space: nowrap;
	/* 文本不换行 */
	margin-top: 15rpx;
	font-size: 16px;
	}

	.movie-note {
		padding: 1%;
		margin-top: 10rpx;
		display: inline-block;
		border-radius: 18%;
		background: #ff5500;
	}
</style>